#gallery{
  position: relative;
  height: 800px;
  background-image: linear-gradient(
    to top right,
    #2e7da1,
    #031e2b
  );

  & .row{
    position: relative;
    height: 760px;
    // border: 2px solid gray;
  }
}

// ------------------- Column One -----------------
#gallery .column-one{
  & .img-container{
    position: absolute;
    top: 200px;
    right: 6px;
    height: 450px;
    width: 300px;
    overflow: hidden;
    // border: 5px solid gray;
    // background-color: greenyellow;
  }

  & .gallery-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// ------------------- Column Two ------------------
#gallery .column-two{
  position: relative;
  top: 200px;
  left: 30px;
  color: white;
  height: 500px;
  max-width: 370px;
  border: 2px solid gray;

  & h5{
    position: absolute;
    top: 50px;
    left: -190px;
    font-size: 15px;
  }

  & p{
    position: relative;
    top: 150px;
    word-wrap: normal;
    width: 250px;
    // border: 2px solid gray;
  }
}


// --------------- Column Three ---------------
#gallery .column-three{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 200px;
  width: 600px;
  // border: 2px solid gold;
}

@keyframes img-to-right{  
  0% {
      transform: translate(0, 0x,);
  }
  100% {
    transform: translate(32px, 0);
  }
}