#titlewrapper {
  position: relative;
  top: 100px;
  left: -50px;
  width: 149px;
  height: 60px;
  // border: 2px solid gray;
}

#titlewrapper h1:nth-child(1) {
  position: absolute;
  top: 0;
  left: -113px;
  width: 120px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  // border: 2px solid gray;
}

#titlewrapper h1:nth-child(2) {
  position: absolute;
  top: 0;
  right: 0;
  color: black;
  left: -3px;
  width: 145px;
  height: 100%;
  white-space: nowrap;
  overflow: hidden;
  // border: 2px solid rgb(177, 71, 71);

  & p {
    position: absolute;
    top: 0;
    right: 5px;
    font-weight: 500;
}
}
